.home-intro::before {
  content: "";
  display: inline-block;
  border-top: 2px solid rgb(255, 205, 111);
  height: 9px;
  width: 35px;
}

.home-intro-space {
  content: "";
  display: inline-block;
  width: 17.5px;
}

@media only screen and (min-width: 2160px) {

  .home-intro {
    letter-spacing: 0.25em;
    color: rgb(255, 205, 111);
    text-shadow: 0 0 15px rgb(0, 0, 0);
    font: normal normal normal 21px/1.4em Roboto Regular;
  }

  .home-text {
    line-height: 5.4rem;
    font: normal normal normal 120px/1.4em PFD-SB;
    text-shadow: 0 0 30px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (min-width: 1311px) and (max-width: 2159px) {

  .home-intro {
    letter-spacing: 0.25em;
    color: rgb(255, 205, 111);
    text-shadow: 0 0 15px rgb(0, 0, 0);
    font: normal normal normal 21px/1.4em Roboto Regular;
  }

  .home-text {
    line-height: 5.4rem;
    /* letter-spacing: -0.03em; */
    font: normal normal normal 100px/1.4em PFD-SB;
    text-shadow: 0 0 20px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (min-width: 768px) and (max-width: 1310px) {

  .home-intro {
    letter-spacing: 0.25em;
    color: rgb(255, 205, 111);
    text-shadow: 0 0 15px rgb(0, 0, 0);
    font: normal normal normal 21px/1.4em Roboto Regular;
  }

  .home-text {
    line-height: 5.4rem;
    font: normal normal normal 60px/1.4em PFD-SB;
    text-shadow: 0 0 30px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (min-width: 580px) and (max-width: 767px) {

  .home-intro {
    letter-spacing: 0.25em;
    color: rgb(255, 205, 111);
    text-shadow: 0 0 15px rgb(0, 0, 0);
    font: normal normal normal 21px/1.4em Roboto Regular;
  }

  .home-text {
    line-height: 5.4rem;
    font: normal normal normal 45px/1.4em PFD-SB;
    text-shadow: 0 0 30px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }
}

@media only screen and (max-width: 579px) {

  .home-intro::before {
    content: "";
    display: inline-block;
    border-top: 1px solid rgb(255, 205, 111);
    height: 6px;
    width: 28px;
  }

  .home-intro {
    letter-spacing: 0.25em;
    color: rgb(255, 205, 111);
    text-shadow: 0 0 15px rgb(0, 0, 0);
    font: normal normal normal 14px/1.4em Roboto Regular;
  }

  .home-text {
    line-height: 5.4rem;
    font: normal normal normal 32px/1.4em PFD-SB;
    text-shadow: 0 0 30px rgb(0, 0, 0);
    color: rgba(255, 255, 255, 0.95);
  }
}