.about-section {
  display: block;
  overflow: auto;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.9989);
  background-size: contain;
  background-repeat: no-repeat;
}

.about-me {
  margin-bottom: 70px;
}

.about-title {
  /* padding-bottom: 15px;
  border-bottom: 3px solid rgb(255, 255, 255); */
  /* color: rgb(241, 174, 48); */
  /* color: rgb(255, 205, 111); */
  color: white;
}

.about-text {
  color: white;
  /* color: rgb(241, 174, 48); */
  /* color: rgb(255, 205, 111); */
}

.orange-text {
  color: white;
  /* color: rgb(241, 174, 48); */
  /* color: rgb(255, 205, 111); */
}

@media only screen and (min-width: 2160px) {
  .about-container {
    margin-top: 18%;
    scale: 1.3;
  }

  .about-title {
    font: normal normal normal 30px/35px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }

  .about-text {
    font: normal normal normal 26px/35px Roboto Regular;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }
}

@media only screen and (min-width: 1311px) and (max-width: 2159px) {
  .about-container {
    margin-top: 10%;
    scale: 0.95;
  }

  .about-title {
    font: normal normal normal 37px/40px Poppins-Bold;
    margin-bottom: 20px;
    /* color: white; */
  }

  .about-text {
    letter-spacing: 0.008em;
    font: normal normal normal 27px/43px Roboto-Medium;
    /* color: white; */
  }
}

@media only screen and (min-width: 768px) and (max-width: 1310px) {
  .about-container {
    margin-top: 20%;
    scale: 0.85;
  }

  .about-title {
    font: normal normal normal 25px/30px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }

  .about-text {
    font: normal normal normal 20px/30px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }
}

@media only screen and (min-width: 580px) and (max-width: 767px) {
  .about-container {
    margin-top: 25%;
    scale: 0.85;
  }

  .about-title {
    font: normal normal normal 20px/25px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }

  .about-text {
    font: normal normal normal 15px/25px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }
}

@media only screen and (max-width: 579px) {
  .about-container {
    scale: 0.8;
    margin-top: 35%;
  }

  .about-title {
    font: normal normal normal 20px/25px Poppins-Bold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }

  .about-text {
    font: normal normal normal 15px/25px Poppins SemiBold;
    text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
    /* color: white; */
  }
}
