.vnav-wrapper {
    min-height: 100vh;
    // width: 100%;
    position: fixed;
    display: flex;
    top: 0;
    right: 0;
    justify-content: flex-end;
    // padding-top: 259px;
    padding-top: 13.5%;
    padding-right: 105px;
}

.vnav {
  writing-mode: sideways-lr;
  -webkit-writing-mode: sideways-lr;
  -ms-writing-mode: sideways-lr;
  text-orientation: sideways;

  &__wrapper {
    position: fixed;
    height: 100%;
    width: 0%;
    background: transparent;

    display: flex;
    flex-flow: column nowrap;
  }
}

.vnav-li {
  position: relative;
  transition: all 0.3s ease-out;
  margin-bottom: 20px;
  text-decoration: none;
  list-style: none;
  display: inline-block;
}

.vnav-a {
  display: block;
  padding: 0;
  color: white;
  transition: all 0.15s ease-out;
  text-decoration: none;
  font: normal normal normal 14px/1.4em Roboto-Medium !important;
  letter-spacing: 0.22em;
}

.vnav-a:hover {
  color: rgb(255, 205, 111);
}